.checkout__section {
  display: flex;
  flex-direction: column;
  align-items: left;
  margin: 2rem auto;
  width: 90%;

  .checkout__subheading {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
  }

  .checkout__label {
    font-size: 1rem;
  }

  .checkout__input {
    height: 2rem;
    border: none;
    border-bottom: 1px solid black;
    margin-bottom: 1rem;
  }
}
